:root {
    --COLORTEXT: #22252A;
    --PRIMARYCOLOR: #8BAA36;
}

.container {
    width: 375px;
    margin: 0 auto;
    padding: 0 16px;
    outline: 1px solid red;
}

.section {
    padding-top: 32px;
    padding-bottom: 50px;
}

.hero {
    width: 100%;
    height: 455px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: gray;
}

.title {
    min-width: 276px;
    color: var(--PRIMARYCOLOR);
    font-family: Poppins_Bold;
    font-size: 24px;
    font-weight: 600;
    line-height: 24px;
    letter-spacing: -0.48px;
    /* padding-top: 144px; */
    margin-bottom: 18px;
}

.subtitle {
    width: 299px;
    color: var(--COLORTEXT);
    font-size: 12px;
    line-height: 16px;
    letter-spacing: -0.24px;
    margin-bottom: 24px;
    text-align: center;
}

.button {
    display: inline-block;
    min-width: 150px;
    height: 35px;
    background-color: transparent;
    border-radius: 24px 44px;
    border: 1px solid green;
    color: var(--COLORTEXT);
    font-size: 10px;
    line-height: normal;
    padding: 10px 18px;
    margin-bottom: 42px;
}

.time_prepeare {
    color: #23262A;
    font-family: Poppins_Medium;
    font-size: 10px;
    font-weight: 500;
    line-height: 14px;
    letter-spacing: -0.24px;
    padding-bottom: 90px;
}

.wrapper_title_table {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--PRIMARYCOLOR);
    width: 100%;
    height: 42px;
    border-radius: 8px;
    padding: 12px 14px;
    margin-bottom: 24px;
}

.title_table {
    color: #FAFAFA;
    font-family: Poppins_Bold;
    font-size: 10px;
    font-weight: 600;
    letter-spacing: 0.3px;
}

.title_table_position {
    margin-left: 122px;
}

/* .wrapper_title_position {
    width: 33%;
    display: flex;
    justify-content: space-between;
    align-items: center;
} */

@media screen and (min-width:767.98px) {
    .container {
        width: 768px;
    }

    .title {
        min-width: 505px;
        font-size: 44px;
        line-height: 44px;
        margin-bottom: 24px;
    }

    .subtitle {
        width: 506px;
        font-size: 18px;
        line-height: 24px;
        letter-spacing: -0.36px;
    }

    .button {
        min-width: 278px;
        height: 59px;
        font-size: 16px;
        padding: 18px 44px;
        margin-bottom: 60px;
    }

    .time_prepeare {
        font-size: 14px;
        line-height: 20px;
        padding-bottom: 32px;
    }

    .section {
        padding-top: 50px;
        padding-bottom: 96px;
    }

    .wrapper_title_table {
        height: 60px;
        padding: 21px 32px;
        margin-bottom: 32px;
    }

    .title_table {
        font-size: 18px;
        letter-spacing: 0.54px;
    }

    .title_table_position {
        margin-left: 312px;
    }
}

@media screen and (min-width:1439.98px) {
    .container {
        width: 1440px;
    }

    .title {
        letter-spacing: -0.88px;
    }

    .subtitle {
        width: 656px;
        margin-bottom: 30px;
    }

    .button {
        min-width: 270px;
        margin-bottom: 48px;
    }

    .section {
        padding-bottom: 100px;
    }

    .wrapper_title_table {
        padding: 21px 40px;
        margin-bottom: 50px;
    }

    .title_table_position {
        margin-left: 761px;
    }
}